import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import Data from './Data';
import styled from 'styled-components';
import './App.css';

const 박스 = styled.div`
  padding-top:30px;
  color : ${ props => props.색상 }
`;


function Detail(props){

  const [alert,alert변경] = useState(true);
  const [inputData, inputData변경] = useState('');


  useEffect(( ) => {

    

    const 타이머 = setTimeout(()=>{ alert변경(false) }, 2000);
    return ()=>{ clearTimeout(타이머) }
  },[]);

  const [shoes, shoes변경] = useState(Data);
  const history = useHistory();
  const { id } = useParams();
  const 찾은상품 = shoes.find(function(상품) {return 상품.id == id} )


  return( 
    
    <div className="container">
      <박스 색상='blue' >Detail</박스>

      <input onChange={(e)=>{ inputData변경(e.target.value) }} />
      {
        alert === true
        ? <div className='my-alert'>
            <p>재고가 얼마 남지 않았습니다</p>
          </div>
        : null
      }
      
      <div className="row">
        <div className="col-md-6">
          <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{ 찾은상품.title }</h4>
          <p> {찾은상품.content} </p>
          <p> { 찾은상품.price}원 </p>

          <Info 재고={props.재고} ></Info>



          <button className="btn btn-danger">주문하기</button> 
          <button className="btn btn-danger" onClick={()=>{history.goBack();}} >뒤로가기</button> 
        </div>
      </div>
    </div> 
  );
}


function Info(props){
  return(
    <p>재고 : {props.재고[0]} </p>
  )
}



export default Detail;